class PicScreen extends React.Component {
  state = {
    slideIndex: 0,
    marks: {},
    max: 0,
    imgshowdist: [],
    tf: "t",
    ModalCount: 100,
    visible: false,
    confirmLoading: false,
    data: [],
    finishMin: 0,
    finishMax: 100
  };

  constructor(props) {
    super(props);
    this.next = this.next.bind(this);
    this.previous = this.previous.bind(this);
  }
  next() {
    this.slider.slickNext();
    if (this.state.data) {
      let mmarks = this.state.marks;
      if ( (this.state.data[this.state.slideIndex].status===null)&&(this.state.data[this.state.slideIndex]) ) {
        mmarks[this.state.slideIndex] =
          this.state.tf === "f"
            ? {
                style: {
                  color: "#DC143C"
                },
                label: <strong>×</strong>
              }
            : {
                style: {
                  color: "#3CB371"
                },
                label: <strong>√</strong>
              };
        this.state.data[this.state.slideIndex].status = this.state.tf === "f" ? 1 : 0;
      }
    }
  }
  previous() {
    this.slider.slickPrev();
  }

  componentWillUmount() {
    document.removeEventListener("keydown", this.handleEenterKey);
  }
  componentDidMount() {}
  handleKeyDown = e => {
    if (e.keyCode === 65) {
      this.previous();
    }
    if (e.keyCode === 68) {
      this.next();
    }
    if (e.keyCode === 83) {
      this.errorMark();
    }
  };

  errorMark = e => {
    let mmarks = this.state.marks;
    let mdata = this.state.data;
    mmarks[this.state.slideIndex] =
      this.state.data[this.state.slideIndex].status !== 1
        ? {
            style: {
              color: "#DC143C"
            },
            label: <strong>×</strong>
          }
        : {
            style: {
              color: "#3CB371"
            },
            label: <strong>√</strong>
          };

    mdata[this.state.slideIndex].status =
      mdata[this.state.slideIndex].status === 1 ? 0 : 1;

    this.setState({ data: mdata, marks: mmarks });
  };
  tfChange = e => {
    this.setState({ tf: e.target.value });
  };
  showModal = () => {
    this.setState({
      visible: true
    });
  };

  handleOk = () => {
    this.picSubmit()
    if (this.state.data.length==0){
          this.setState({
        confirmLoading: true
      });
      const _this = this;
      var data = new FormData();
      data.append("count", this.state.ModalCount);
      axios
        .post("/aidsp/project/imgget/", data)
        .then(function(response) {
          let data = response.data;
          _this.setState({ data: data });
          let imgShow = [];
          for (let item of data) {
            imgShow.push(<img src={"/aidsp" + item["url"]} alt="" class="spic"/>);
          }
          _this.setState({ imgshowdist: imgShow, max: response.data.length - 1 });
          _this.setState({
            visible: false,
            confirmLoading: false
          });
        })
        .catch(function(error) {
          console.log(error);
          _this.setState({
            visible: false,
            confirmLoading: false
          });
        });
    }
  
      

  };

  handleCancel = () => {
    console.log("Clicked cancel button");
    this.setState({
      visible: false
    });
  };
  ModalCountonChange = value => {
    this.setState({ ModalCount: value });
  };
  picSubmit = () => {
    if (this.state.data.length!=0){
      const _this = this;
      axios
        .post("/aidsp/project/imgpost/", this.state.data)
        .then(function(response) {
          Modal.success({
            content: "提交成功",
            onOk:()=>{
               _this.setState({
                slideIndex: 0,
                marks: {},
                max: 0,
                imgshowdist: [],
                data: [],
            })
            }
          });
        })
        .catch(function(error) {
          Modal.error({
            content: "错误",
            onOk() {}
          });
        });
    }
   
  };
  render() {
    var settings = {
      infinite: false,
      dots: false,
      speed: 0,
      slidesToShow: 1,
      slidesToScroll: 1,
      afterChange: () =>
        this.setState(state => ({ updateCount: state.updateCount + 1 })),
      beforeChange: (current, next) => this.setState({ slideIndex: next })
    };
    const { visible, confirmLoading } = this.state;

    return (
      <div >
        <div onKeyDown={this.handleKeyDown}> 
        <p>当前: {this.state.slideIndex} </p>
        <AntdSlider
          onChange={e => this.slider.slickGoTo(e)}
          value={this.state.slideIndex}
          min={0}
          max={this.state.max}
          marks={this.state.marks}
        />
        <br />
        <Button
          className="button"
          onClick={this.previous}
          style={{ marginRight: 8 }}
        >
          上一张
        </Button>
        <Button
          className="button"
          onClick={this.next}
          style={{ marginRight: 8 }}
        >
          下一张
        </Button>
        <Radio.Group
          value={this.state.tf}
          onChange={this.tfChange}
          style={{ marginRight: 8 }}
        >
          <Radio.Button value="t">默认√</Radio.Button>
          <Radio.Button value="f">默认×</Radio.Button>
        </Radio.Group>
        <Button
          className="button"
          style={{ marginRight: 20 }}
          onClick={this.errorMark}
        >
          标记
        </Button>
        {(() =>{ if(this.state.data[this.state.slideIndex]){
          switch(this.state.data[this.state.slideIndex].status){
             case 1:return <Icon type="close-circle" theme="twoTone" twoToneColor="#DC143C " />; break;
             case 0:return <Icon type="check-circle" theme="twoTone" twoToneColor="#3CB371 " />; break;
             default:return null;
          }
        }}
       )()}
        <br />
        <br />
        <Button
          className="button"
          style={{ marginRight: 8 }}
          onClick={this.showModal}
        >
          获取图片
        </Button>
        <Modal
          title="Title"
          visible={visible}
          onOk={this.handleOk}
          confirmLoading={confirmLoading}
          onCancel={this.handleCancel}
        >
          <InputNumber
            min={1}
            max={300}
            value={this.state.ModalCount}
            onChange={this.ModalCountonChange}
          />
        </Modal>
        <Button
          className="button"
          style={{ marginRight: 20 }}
          onClick={this.picSubmit}
        >
          提交
        </Button>
        <br />
        <br />
          <Slider
            style={{ width: "600px" }}
            ref={slider => (this.slider = slider)}
            {...settings}
          >
            {this.state.imgshowdist}
          </Slider>
        <br />
        <br />

        <Text code>已完成图片</Text>
        <br />
        <br />
        </div>
        <div>
        <PicScreen_2 />
        </div>
      </div>

    );
  }
}
class PicScreen_2 extends React.Component {
  state = {
    slideIndex: 0,
    marks: {},
    max: 0,
    imgshowdist: [],
    ModalCount: 100,
    visible: false,
    confirmLoading: false,
    data: [],
    finishMin: 0,
    finishMax: 100,
    tfshow: 'all',
    alldata:[],
    tdata:[],
    fdata:[],
  };

  constructor(props) {
    super(props);
    this.next = this.next.bind(this);
    this.previous = this.previous.bind(this);
  }
  next() {
    if (this.state.slideIndex < this.state.data.length - 1){
      this.slider.slickNext();
      if(this.state.slideIndex >= this.state.finishMax){
        this.setState({
          finishMax:this.state.slideIndex+1,
          finishMin:this.state.slideIndex-99,
        })
      }
      if (this.data) {
        let mmarks = this.state.marks;
        if (!mmarks[this.state.slideIndex]) {
          mmarks[this.state.slideIndex] =
            this.state.tf === "f"
              ? {
                  style: {
                    color: "#DC143C"
                  },
                  label: <strong>×</strong>
                }
              : {
                  style: {
                    color: "#3CB371"
                  },
                  label: <strong>√</strong>
                };
          this.data[this.state.slideIndex].status = this.state.tf === "f" ? 1 : 0;
        }
      }
    }
  }
  previous() {
    if (this.state.slideIndex !== 0){
      this.slider.slickPrev();
      if(this.state.slideIndex <= this.state.finishMin){
        this.setState({
          finishMax:this.state.slideIndex+99,
          finishMin:this.state.slideIndex-1,
        })
      }
    }
  }

  componentWillUmount() {
    document.removeEventListener("keydown", this.handleEenterKey);
  }
  componentDidMount() {
    const _this = this;
    axios
      .get("/aidsp/project/imgfinish/")
      .then(function(response) {
        let data = response.data;
        if (data.length!=0){
          _this.setState({ 
            data: data,
            alldata:data,
            tdata:data.filter(item => item.status === 0),
            fdata:data.filter(item => item.status === 1)
          });
          let imgShow = [];
          var i = 0;
          let mmarks = _this.state.marks;
          for (let item of data) {
            imgShow.push(<img src={"/aidsp" + item["url"]} alt="" class="spic"/>);
            mmarks[i] =
              data[i].status === 1
                ? {
                    style: {
                      color: "#DC143C"
                    },
                    label: <strong>×</strong>
                  }
                : {
                    style: {
                      color: "#3CB371"
                    },
                    label: <strong>√</strong>
                  };
            i = i + 1;
          }
          _this.setState({
            imgshowdist: imgShow,
            finishMax: response.data.length>100?100:response.data.length - 1,
            marks: mmarks
          });
        }
 
      })
      .catch(function(error) {
        console.log(error);
      });
  }
  handleKeyDown = e => {
    if (e.keyCode === 65) {
      this.previous();
    }
    if (e.keyCode === 68) {
      this.next();
    }
    if (e.keyCode === 83) {
      this.errorMark();
    }
  };

  errorMark = e => {
    let mmarks = this.state.marks;
    let mdata = this.state.data;
    mmarks[this.state.slideIndex] =
      this.state.data[this.state.slideIndex].status !== 1
        ? {
            style: {
              color: "#DC143C"
            },
            label: <strong>×</strong>
          }
        : {
            style: {
              color: "#3CB371"
            },
            label: <strong>√</strong>
          };

    mdata[this.state.slideIndex].status =
      mdata[this.state.slideIndex].status === 1 ? 0 : 1;

    this.setState({ data: mdata, marks: mmarks });
  };

  picSubmit = () => {
    const _this = this;
    axios
      .post("/aidsp/project/imgpost/", this.state.alldata)
      .then(function(response) {
        Modal.success({
          content: "提交成功",
          onOk() {}
        });
      })
      .catch(function(error) {
        Modal.error({
          content: "错误",
          onOk() {}
        });
      });
  };
  percentageOnChange = value => {
    if (this.state.data.length > 100){
      if (value * 0.01 * (this.state.data.length - 100) > this.state.slideIndex){
        this.setState({slideIndex: parseInt( value * 0.01 * (this.state.data.length - 100) )})
      }
      if (value * 0.01 * (this.state.data.length - 100) + 100 < this.state.slideIndex){
        this.setState({slideIndex: parseInt( value * 0.01 * (this.state.data.length - 100) + 100 )})
      }
      this.setState({
        finishMin: parseInt( value * 0.01 * (this.state.data.length - 100)),
        finishMax:  parseInt( value * 0.01 * (this.state.data.length - 100) + 100 ),
      });
    }
    else{
    }
  };
  showChange =  e => {
    this.slider.slickGoTo('0')
    this.setState({ tfshow: e.target.value });
    var data;
    if(e.target.value==='all'){
      this.setState({data:this.state.alldata})
      this.state.data = this.state.alldata
    }
    else if (e.target.value==='cha'){
      this.setState({data:this.state.fdata})
      this.state.data = this.state.fdata
    }
    else if (e.target.value==='gou'){
      this.setState({data:this.state.tdata})
      this.state.data = this.state.tdata
    }
    let imgShow = [];
    var i = 0;
    let mmarks = this.state.marks;
    mmarks = []
    data = this.state.data
    for (let item of data) {
      imgShow.push(<img src={"/aidsp" + item["url"]} alt="" class="spic"/>);
      mmarks[i] =
        data[i].status === 1
          ? {
              style: {
                color: "#DC143C"
              },
              label: <strong>×</strong>
            }
          : {
              style: {
                color: "#3CB371"
              },
              label: <strong>√</strong>
            };
      i = i + 1;
    }
    this.setState({
      imgshowdist: imgShow,
      finishMin: 0,
      finishMax: data.length>100?100:data.length - 1,
      marks: mmarks
    });
  }
  silderOnchang = e =>{
    try{
      this.slider.slickGoTo(e)
    }catch(ex){
      console.log('错误'+ex)
    }
  }
  render() {
    var settings = {
      lazyLoad:true,
      dots: false,
      speed: 0,
      slidesToShow: 1,
      slidesToScroll: 1,
      lazyLoad: true,
      infinite: false,
      afterChange: () =>
        this.setState(state => ({ updateCount: state.updateCount + 1 })),
      beforeChange: (current, next) => this.setState({ slideIndex: next })
    };
    const { visible, confirmLoading } = this.state;

    return (
      <div onKeyDown={this.handleKeyDown}>
        <p>当前: {this.state.slideIndex} </p>
        <AntdSlider
          onChange={this.silderOnchang}
          value={this.state.slideIndex}
          min={this.state.finishMin}
          max={this.state.finishMax}
          marks={this.state.marks}
        />
        <br />
        <AntdSlider
          min={0}
          max={100}
          onChange={this.percentageOnChange}
          marks={{
            0: "0%",
            100: "100%"
          }}
        />
        <br />
        <Button
          className="button"
          onClick={this.previous}
          style={{ marginRight: 8 }}
        >
          上一张
        </Button>
        <Button
          className="button"
          onClick={this.next}
          style={{ marginRight: 8 }}
        >
          下一张
        </Button>
        <Radio.Group
          value={this.state.tfshow}
          onChange={this.showChange}
          style={{ marginRight: 8 }}
        >
          <Radio.Button value="all">全部显示</Radio.Button>
          <Radio.Button value="cha">只显示×</Radio.Button>
          <Radio.Button value="gou">只显示√</Radio.Button>
        </Radio.Group>
        <Button
          className="button"
          style={{ marginRight: 20 }}
          onClick={this.errorMark}
        >
          标记
        </Button>
        {(() =>{ if(this.state.data[this.state.slideIndex]){
          switch(this.state.data[this.state.slideIndex].status){
             case 1:return <Icon type="close-circle" theme="twoTone" twoToneColor="#DC143C " />; break;
             case 0:return <Icon type="check-circle" theme="twoTone" twoToneColor="#3CB371 " />; break;
             default:return null;
          }
        }}
       )()}
        <br />
        <br />
        <Modal
          title="Title"
          visible={visible}
          onOk={this.handleOk}
          confirmLoading={confirmLoading}
          onCancel={this.handleCancel}
        >
          <InputNumber
            min={1}
            max={300}
            value={this.state.ModalCount}
            onChange={this.ModalCountonChange}
          />
        </Modal>
        <Button
          className="button"
          style={{ marginRight: 20 }}
          onClick={this.picSubmit}
          onKeydown={this.handleKeyDown}
        >
          提交
        </Button>
        <br/>
        <br/>
        <Slider
          style={{ width: "600px" }}
          ref={slider => (this.slider = slider)}
          {...settings}
        >
        {this.state.imgshowdist}
        </Slider>
      </div>
    );
  }
}

const { Header, Sider, Content } = Layout;

class SiderDemo extends React.Component {
  state={
    main_show :Personal,
        collapsed: false,

  }
  toggle = () => {
    this.setState({
      collapsed: !this.state.collapsed,
    });
  };
  menuSelect = e =>{
    if (e.key == "/aidsp/project"){
      this.setState({main_show:App})
    }
    if (e.key == "/aidsp/dataset"){
      this.setState({main_show:DataSetApp})

    }
  if (e.key == "/aidsp/personal"){
      this.setState({main_show:Personal})

    }
  if (e.key == "/aidsp/picscreen"){
      this.setState({main_show:PicScreen})

    }
  }
  pagechange = (key )=>{

    if (key == "/aidsp/project"){
      this.setState({main_show:App})
    }
    if (key == "/aidsp/dataset"){
      this.setState({main_show:DataSetApp})
    }
    if (key == "/aidsp/personal"){
      this.setState({main_show:Personal})
    }
    if (key == "/aidsp/picscreen"){
      this.setState({main_show:PicScreen})

    }
  }
  componentDidMount() {
    this.pagechange(window.location.pathname)
  }
  render(){
    const pathName = window.location.pathname;
    return(
        <BrowserRouter>
        <Layout style={{ minHeight: '100vh',height:'100%',width:'100%',border: '1px',position:'fixed',left:0,top:0}}>
          <Sider trigger={null} collapsible collapsed={this.state.collapsed} >
		      <div className="logo">
		      <h1 style={{color:'#ffffff',
		      textAlign:'center',
		      fontSize: '20px',
		      }} >{!this.state.collapsed?'数据集生产管理':''}</h1>
		      </div>       
          	  <Menu theme="dark" mode="inline" defaultSelectedKeys={[pathName]} onSelect={this.menuSelect}>
              <Menu.Item key="/aidsp/personal">
                <Link to="/aidsp/personal">
              <Icon type="user" />
                <span>个人</span>
                </Link>
              </Menu.Item>
              <Menu.Item key="/aidsp/project">
                <Link to="/aidsp/project">
              <Icon type="appstore-o" />
                <span>项目</span>
                </Link>
              </Menu.Item>
              <Menu.Item key="/aidsp/dataset">
                  <Link to="/aidsp/dataset">
              <Icon type="appstore-o" />
                <span>数据集</span>
                  </Link>

              </Menu.Item>
              <Menu.Item key="/aidsp/picscreen">
                  <Link to="/aidsp/picscreen">
              <Icon type="picture" />
                <span>图片筛选</span>
                  </Link>

              </Menu.Item>
            </Menu>
          </Sider>
          <Layout>
            <Header style={{ background: '#fff', padding: 0 }}>
              <Icon
                className="trigger"
                type={this.state.collapsed ? 'menu-unfold' : 'menu-fold'}
                onClick={this.toggle}
              />
            </Header>
            <Content
              style={{
                margin: '24px 16px',
                padding: 24,
                background: '#fff',
                minHeight: 280,
                overflowY: 'auto',
                overflowX: 'hidden',
              }}
            >
              <this.state.main_show/>
            </Content>
          </Layout>
        </Layout>
        </BrowserRouter>
        )

            
    }
}
ReactDOM.render(<SiderDemo />, document.getElementById('container'));

